import React from "react";
class Calc extends React.Component {
  constructor() {
    super();
    this.state = {
      result: 0
    };
  }
  addition = () => {
    this.setState({
      result: this.state.result + 2
    });
  };
  square = () => {
    this.setState({
      result: this.state.result * this.state.result
    });
  };
  double = () => {
    this.setState({
      result: this.state.result * 2
    });
  };
  divide = () => {
    const { result } = this.state;
    if (result <= 0) {
      return;
    }
    this.setState({
      result: this.state.result / 2
    });
  };
  subtract = () => {
    const { result } = this.state;
    if (result <= 0) {
      return;
    }
    this.setState((prevState) => {
      return {
        result: prevState.result - 2
      };
    });
  };
  render() {
    const { result } = this.state;
    return (
      <div className="calc-item">
        <div className="result-block">
          <div style={{ fontSize: 40 }}>{result}</div>
        </div>
        <hr />
        <div className="button-block">
          {/* Buttons */}
          <button alt="Add" className="action-button" onClick={this.addition}>
            Add 2!
          </button>
          <button alt="Square" className="action-button" onClick={this.square}>
            Square!
          </button>
          <button alt="Double" className="action-button" onClick={this.double}>
            Double!
          </button>
          <button alt="Divide" className="action-button" onClick={this.divide}>
            Divideby2!
          </button>
          <button
            alt="Subtract"
            className="action-button"
            onClick={this.subtract}
          >
            Subtract2!
          </button>
        </div>
      </div>
    );
  }
}
export default Calc;
